<template>
    <div class="img_wrap">
        <div class="img_list">
            <p class="color">颜色</p>
            <p class="type">车款</p>
        </div>
        <div class="img" @scroll="scroll" ref="scroll">
            <div ref="element" class="ele">
                <img v-for="(item, index) in list" :key="index" :src="item.Url.replace('{0}', item.LowSize)">
            </div>
        </div>
    </div>
</template>
<script>
    import {mapState, mapActions} from 'vuex';
    export default{
        computed:{
            ...mapState({
                list: state=>state.img.list
            })
        },
        methods: {
            ...mapActions({
                getImgDetailList: 'img/getImgDetailList'
            }),
            scroll(){
                console.log(2)
                let scrollEle = this.$refs.scroll.scrollTop;
                let elementHeight = this.$refs.element.getBoundingClientRect().height;
                if (scrollEle + window.innerHeight + 20 > elementHeight){
                    this.getImgDetailList();
                }
            }
        },
        mounted(){
            // 获取数据列表
            this.getImgDetailList();
        }
    }
</script>
<style lang="scss" scoped>
    .img{
        height: 100%;
        overflow: scroll;
        margin-top:.8rem;
        .ele{
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            img{
                width: 32.5%;
                height: 1.6rem;
                margin:.05rem .05rem 0 0;
                box-sizing: border-box;
            }
        }
    }
    
    .img_wrap{
        width: 100%;
        height: 100%;
        background: #f4f4f4;
        overflow: hidden;
    }
    .img_list{
        position: fixed;
        background: #fff;
        top: 0;
        width: 100%;
        height: .8rem;
        -webkit-box-align: center;
        display: flex;
        align-items: center;
        box-sizing: border-box;
        z-index: 99;
        p{
            -webkit-box-flex: 1;
            flex: 1;
            text-align: center;
            box-sizing: border-box;
            color: #454545;
            display: inline-block;
        }
        .type{
            border-left: 1px solid #ececec;
        }
    }
</style>


